@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: grid;
  width:400px;
  height: 606px;
  margin:auto;
  margin-top:150px;
  padding: 10px;
  border: 1px solid black;
  background-color: #080708;
}
.credits{
  text-align: center;
  font-family: 'Orbitron';
  font-size: 18px;
}
button{
  font-size: 20px;
  font-family: 'Orbitron', sans-serif;
}
button:hover{
  opacity:0.8;
}
button:active{
  scale:0.95;
}
.text{
  position: relative;
  height:100px;
  width: 100%;
  padding: 0;
  border: none;
  text-align: right;
  background-color: #080708;
  color: white;
  font-family: 'Orbitron', sans-serif;
}
#inputUpper{
  font-size: 16px;
  height: 14px;
  margin-top: 30px;
  color: #cc9e08;
}
#inputLower{
  font-size: 30px;
  height: 60px;
}
.clear,.zero{
  width:200px;
  height:100px;
}
.zero,.decimal{
  position:relative;
  bottom:50px;
}
.operator,.number,.decimal{
  width:100px;
  height:100px;
}
.equals{
  position:relative;
  width:100px;
  height:200px;
  bottom:100px;
  background-color: #3772FF;
  border: 1px solid #080708;
  color: white;
}
.clear{
  background-color: #DF2935;
  border: 1px solid #080708;
  color: white;
}
.decimal,.number,.zero{
  background-color: #294936;
  border: 1px solid #080708;
  color: white;
}
.operator{
  background-color: #5B8266;
  border: 1px solid #080708;
  color: white;
}



